<template>
  <div id="test">
      <div class="row justify-content-md-center" v-for="(question, index) in questions" v-if="showTest">
        <div class="coloredCol col col-sm-1 border" v-bind:class="[colorClass[index]]"></div>
        <div class="questionCol col col-sm-5 border">{{question.question}}</div>
        <div class="answersCol col col-sm-5 border">
          <div class="form-check" v-for="(answer, jndex) in question.answers">
            <label>
              <input class="form-check-input" :name="index" :value="jndex" type="radio" :disabled="showCorrectAnswers" v-model="question.givenAnswer">
              <span>{{pointers[jndex]}}- {{answer.text}}</span>
            </label>
          </div>
        </div>
        <div class="resultCol col col-sm-1 border">
          <div v-show="showCorrectAnswers">{{pointers[question.correctAnswerIndex]}}</div>
        </div>
      </div>

      <div class="row justify-content-md-center">
        <div class="col col-sm-3">
          <button class="btn" v-on:click="restart" v-bind:disabled="!showCorrectAnswers">Restart</button>
        </div>
        <div class="col col-sm-3">
          <button class="btn" v-on:click="retry" v-bind:disabled="!showCorrectAnswers">Retry</button>
        </div>
        <div class="col col-sm-3">
          <button class="btn" v-on:click="retryOnlyWrong" v-bind:disabled="!showCorrectAnswers">Retry wrong</button>
        </div> 
        <div class="col col-sm-3">
          <button class="btn" v-on:click="refresh">Refresh</button>
        </div>
        <div class="col col-sm-3">
          <button class="btn" v-on:click="getResults">Get Results</button>
        </div>
      </div>
      <Results v-if="showResults" v-bind:parameters = "parameters" v-bind:questions = "questions" ref="Results"/>
      <div v-if="showModal">
        <transition name="modal">
          <div class="modal-mask">
            <div class="modal-wrapper">
              <div class="modal-dialog">
                <div class="modal-content">
                  <div class="modal-body">
                    <div class="container-fluid">
                      <div class="row justify-content-md-center">
                        <div class="col">
                          {{text}}
                        </div>
                      </div>
                    </div>  
                  </div>
                  <div class="modal-footer">
                    <div class="container-fluid">
                      <div class="row justify-content-md-center">
                        <div class="col-md">
                          <button type="button" class="btn" @click="shufflePosition">
                            YES
                          </button>                          
                        </div>
                        <div class="col-md">
                          <button type="button" class="btn" @click="noShufflePosition">
                            NO
                          </button> 
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
  </div>
</template>

<script>
import router from '../router'
import Results from './Results'
export default {
  name: 'Test',
  components: {
    Results
  },
  props: ["allQuestions", "parameters"],
  data () {
    return {
      pointers:  ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H'],
      colorClass:[],
      questions: [],
      showCorrectAnswers: false,
      nQuestions: this.parameters.nQuestions,
      showResults: false,
      showModal: false,
      test: null,
      showTest: false
    }
  },

  mounted(){
    if (this.$refs.Results) this.$refs.Results.clear()
    this.prepareQuestions()
    this.getQuestions()
    this.showTest= true
  },

  methods:{
    shuffle(array) {
      let counter = array.length;

      // While there are elements in the array
      while (counter > 0) {
          // Pick a random index
          let index = Math.floor(Math.random() * counter);

          // Decrease counter by 1
          counter--;

          // And swap the last element with it
          let temp = array[counter];
          array[counter] = array[index];
          array[index] = temp;
      }
      return array;
    },

    shuffleQuestions(questions){
      questions = this.shuffle(questions)
      return questions
    },

    shuffleAnswers(questions){
      for (let i = 0; i < questions.length; i++) questions[i].answers = this.shuffle(questions[i].answers)
      return questions
    },

    prepareQuestions(){
      this.allQuestions = this.shuffleQuestions(this.allQuestions)
      this.allQuestions = this.shuffleAnswers(this.allQuestions)
    },
    
    getQuestions(){
      for ( let i = 0; i < this.nQuestions; i++){
        this.questions.push({
          question : this.allQuestions[i].question,
          answers : this.allQuestions[i].answers,
          givenAnswer : null,
          correctAnswerIndex: this.allQuestions[i].answers.indexOf(this.allQuestions[i].answers.find( c => c.isCorrect))
        })
      }
    },

    getResults(){
      // router.push({ name: 'Results', params: {questions: this. questions, nQuestions: this.nQuestions, correctAnswersPoints: this.correctAnswersPoints, noAnswersPoints: this.noAnswersPoints, wrongAnswersPoints: this.wrongAnswersPoints}})
      this.showCorrectAnswers = true
      for(let i = 0; i < this.questions.length; i++){
        if (!this.questions[i].givenAnswer & this.questions[i].givenAnswer != 0) {
          this.colorClass.push("background bg-warning")
        }
        else if (this.questions[i].answers[this.questions[i].givenAnswer].isCorrect) this.colorClass.push("background bg-success")
        else this.colorClass.push("background bg-danger")     
      }
      this.showResults = true
    },

    refresh(){
      
      window.scrollTo(0,0)
      if (this.$refs.Results) this.$refs.Results.clear()
      this.clear()
      this.nQuestions = this.parameters.nQuestions
      this.prepareQuestions()
      this.getQuestions()
      this.showTest= true
    },

    restart(){
      window.scrollTo(0,0)
      if (this.$refs.Results) this.$refs.Results.clear()
      this.showResults = false
      this.showCorrectAnswers = false
      this.colorClass = []

      for (let i = 0; i < this.questions.length; i++){
        this.questions[i].givenAnswer = null
      }
      //this.showTest= false
      this.showModal = true
      this.text = "Restart same test - Would you like to shuffle the position of questions and answers?"
    },

    retryOnlyWrong(){
      window.scrollTo(0,0)
      if (this.$refs.Results) this.$refs.Results.clear()
      this.showResults = false
      this.showCorrectAnswers = false
      this.colorClass = []
      this.questions = this.questions.filter((question) => {
        if (question.givenAnswer) return question.givenAnswer != question.correctAnswerIndex
        else return !question.givenAnswer
      })
      for (let i = 0; i < this.questions.length; i++){
        this.questions[i].givenAnswer = null
      }
      this.showModal = true
      this.text = "Restart same test with only wrong and no answers - Would you like to shuffle the position of questions and answers?"
    },

    retry(){
      window.scrollTo(0,0)
      if (this.$refs.Results) this.$refs.Results.clear()
      this.showResults = false
      this.showCorrectAnswers = false
      this.colorClass = []
      let wrongQuestions = this.questions.filter((question) => {
        if (question.givenAnswer) return question.givenAnswer != question.correctAnswerIndex
        else return !question.givenAnswer
      })

      let wrongQuestionsArray = wrongQuestions.map((question) => {
        return question.question
      })


      let notChosenQuestions = this.allQuestions.filter((question) => {
        return !wrongQuestionsArray.includes(question.question)
      })
      for (let i = 0; i < this.questions.length; i++){

        if (this.questions[i].givenAnswer){
          if (this.questions[i].givenAnswer == this.questions[i].correctAnswerIndex){
            let chosenQuestion = notChosenQuestions.pop()
            this.questions[i] = {
              question: chosenQuestion.question,
              answers: chosenQuestion.answers,
              givenAnswer : null,
              correctAnswerIndex: chosenQuestion.answers.indexOf(chosenQuestion.answers.find( c => c.isCorrect))
            }
          }
          else{
            this.questions[i].givenAnswer = null
          }
        }
        else{
          this.questions[i].givenAnswer = null
        }

      }
      wrongQuestions = null
      wrongQuestionsArray = null
      //this.showTest= false
      this.showModal = true
      this.text = "Retry wrong o no answered questions with other random questions - Would you like to shuffle the position of questions and answers?"
    },

    shufflePosition(){
      this.questions = this.shuffle(this.questions)
      this.questions = this.shuffleAnswers(this.questions)
      for (let i = 0; i < this.questions.length; i++){
        this.questions[i].correctAnswerIndex = this.questions[i].answers.indexOf(this.questions[i].answers.find( c => c.isCorrect))
      }
      this.showModal = false
      this.text = null
      this.showTest= true
    },

    noShufflePosition(){
      this.showModal = false
      this.text = null
      this.showTest= true
    },

    clear(){
      this.showResults = false
      this.questions = []
      this.showCorrectAnswers = false
      this.colorClass = []
      this.nQuestions = null
      if (this.$refs.Results) this.$refs.Results.clear()
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
</style>



